<template>
  <div>
    <el-row class="search">
      <el-col :span="24">
        <el-form :inline="true" :model="searchForm" class="demo-form-inline">
          <el-form-item label="合作商搜索：">
            <el-input
              v-model="searchForm.name"
              placeholder="请输入"
              size="medium"
              style="width: 203px"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              icon="el-icon-search"
              size="medium"
              style="width: 80px"
              @click="getPartner(searchForm)"
              >查询</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <el-row class="list">
      <el-col :span="24">
        <el-row class="add"
          ><el-col
            ><el-button
              icon="el-icon-circle-plus-outline"
              @click="(type = 'add') && (showDialog = true)"
              >新建</el-button
            ></el-col
          ></el-row
        >
        <el-row class="table">
          <el-col>
            <NPTable :partnerList="partnerList" @handlePreview="handlePreview" @handleEdit="handleEdit" @handleDelete="handleDelete" @resetPassword="resetPassword" :loading="loading"></NPTable>
          </el-col>
        </el-row>
        <el-row class="page">
          <el-col :span="20">
            <span
              >共{{ page.totalCount }}条记录&nbsp;&nbsp;第&nbsp;{{
                page.pageIndex
              }}/{{ page.totalPage }}&nbsp;页</span
            >
          </el-col>
          <el-col :span="2">
            <el-button
              @click="(page.pageIndex > 1 ? page.pageIndex-- : page.pageIndex) && getPartner()"
              :disabled="page.pageIndex === 1"
              :class="{ disable: page.pageIndex === 1 }"
              >上一页</el-button
            >
          </el-col>
          <el-col :span="2">
            <el-button
              @click="(page.pageIndex < page.totalPage ? page.pageIndex++ : page.pageIndex) && getPartner()"
              :disabled="page.pageIndex === +page.totalPage"
              :class="{ disable: page.pageIndex === +page.totalPage }"
              >下一页</el-button
            >
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <NPDialog :visible.sync="showDialog" :type="type" :partnerList="partnerList" :currentRow="currentRow" ref="npDialog"></NPDialog>
  </div>
</template>

<script>
import NPTable from './component/NPTable.vue'
import NPDialog from './component/NPDialog.vue'
import { partner, delPartner } from '@/api/node'
export default {
  components: {
    NPTable,
    NPDialog
  },
  data () {
    return {
      searchForm: {
        name: ''
      },
      type: '',
      page: {
        pageIndex: 1,
        pageSize: 10,
        totalCount: 0,
        totalPage: 0
      },
      showDialog: false,
      loading: false,
      partnerList: [],
      currentRow: {}
    }
  },
  created () {
    this.getPartner()
  },
  methods: {
    async getPartner (search) {
      try {
        this.loading = true
        let res = {}
        if (search) {
          res = await partner({ pageIndex: this.page.pageIndex, pageSize: this.page.pageSize, name: search.name })
        } else {
          res = await partner({ pageIndex: this.page.pageIndex, pageSize: this.page.pageSize })
        }
        this.partnerList = res.currentPageRecords
        this.page.totalPage = res.totalPage
        this.page.totalCount = res.totalCount
      } finally {
        this.loading = false
      }
    },
    resetPassword (index, row) {

    },
    handleEdit (index, row) {
      console.log(row)
      this.type = 'edit'
      this.showDialog = true
      this.$refs.npDialog.partnerForm = { account: row.account, contact: row.contact, mobile: row.mobile, name: row.name, password: row.password, ratio: row.ratio, id: row.id }
    },
    handlePreview (index, row) {
      this.type = 'preview'
      this.showDialog = true
      this.currentRow = row
    },
    async handleDelete (index, row) {
      // console.log(row)
      try {
        await delPartner({ id: row.id, name: row.name, pageIndex: this.page.pageIndex, pageSize: this.page.pageSize })
      } catch (e) {
        console.log(e)
        this.$notify.info({
          title: '提示',
          iconClass: 'message-icon',
          message: e.response.data
        })
      }
    }
  }
}
</script>

